html {
  box-sizing: border-box;
  font-size: 16px;
  color: black;
}

li,
ol,
ul {
  list-style: none;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

.notes__list {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  flex-wrap: wrap;
}

.notes {
  margin: 5px;
  text-align: center;
  font-size: 1rem;
}

.nav__list {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
}

.nav__list_active {
  color: white;
}

.notes__tags {
  color: blue;
}

.nav {
  display: flex;
  align-items: center;
  justify-content: center;
}

.nav+.nav {
  margin-left: 15px;
}

span {
  font-size: inherit;
}

.form {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  margin-bottom: 10px;
}

.form__input {
  max-width: 350px;
  margin-bottom: 10px;
}

.popup {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgb(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
}

.popup__container {
  padding: 10px 15px;
  width: 50vh;
  height: 350px;
  background-color: white;
  margin: 0 auto;
}

.change__form__btn {
  margin-top: 15px;
}